.page-hot .header {
  text-align: center;
  padding: 60px 0 28px;
}
.page-hot .searchbox {
  display: block;
  height: 52px;
  line-height: 50px;
  margin: 0 17px;
  padding: 0 18px;
  border: 1px solid #333;
  border-radius: 2px;
  background: url(/portal/static/media/search.d3cc8a70.svg) no-repeat 94% center #fff;
  background-size: 24px;
  color: #9b9b9b;
  font-size: 18px;
}
.page-hot .hot-list {
  margin: 0 17px;
}
.page-hot .hot-list .hot-tit {
  height: 16px;
  line-height: 16px;
  margin: 50px 0 20px;
  text-align: right;
  font-size: 14px;
  color: #999;
  background: url(data:image/png;base64,) no-repeat left center;
  background-size: auto 100%;
}
.page-hot .hot-list .hot-item {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e8e8e8;
  padding: 10px 0;
  font-size: 16px;
  line-height: 28px;
  color: #222;
}
.page-hot .hot-list .hot-item:last-of-type {
  border-bottom: 0;
}
.page-hot .hot-list .hot-item .rank {
  width: 28px;
  font-weight: bolder;
  color: #999;
}
.page-hot .hot-list .hot-item .rank.top {
  color: #f04142;
}
.page-hot .hot-list .hot-item .word {
  flex: 1 1;
}
.page-hot .hot-list .hot-item .word .tag {
  display: inline-block;
  vertical-align: middle;
  height: 15px;
  width: 16px;
  font-size: 10px;
  line-height: 15px;
  text-align: center;
  border-radius: 2px;
  color: #fff;
  margin-left: 8px;
}
.page-hot .hot-list .hot-item .word .tag.hot {
  background-color: #ff7429;
}
.page-hot .hot-list .hot-item .word .tag.new {
  background-color: #ffba0f;
}
.page-hot .hot-list .hot-item .num {
  width: 70px;
  text-align: right;
  color: #999;
  font-size: 12px;
}
